 <template>
  <el-main class="main">
    <div class="comName">企业名称： {{comName}}</div>
    <div class="btn-box">
      <el-button @click="addGoods">新增商品</el-button>
      <el-button>商品计划管理</el-button>
    </div>
    <el-table :data="goodsList">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="goodsName" label="商品名称">
        </el-table-column>
        <el-table-column prop="servers" label="商品包含服务">
        </el-table-column>
        <el-table-column prop="plan" label="所属计划">
        </el-table-column>
        <el-table-column prop="availTime" label="生效时间">
        </el-table-column>
        <el-table-column prop="expiryTime" label="失效时间">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="undercarriage(scope.row)">下架</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>
  </el-main>
 </template>

 <script>
 import goodsList from '../../data/goodsList'
 export default {
   name: 'goodsManage',
   data() {
     return {
       comName: this.$route.query.comName,
       goodsList: Array(6).fill(goodsList),
       currentPage: 1
     }
   },
   methods: {
     handleClick() {
       this.$router.push('addOrEditGoods?type=edit')
     },
     undercarriage() {

     },
     addGoods() {
       this.$router.push('addOrEditGoods?type=add')
     },
     handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
    }
 }
 </script>
 
 <style lang="less" scoped>
  .main{
    text-align: left;
  }
  .btn-box{
    padding: 20px 0;
    border-bottom: 1px solid #eee;
  }
  .comName{
    border-bottom: 1px solid #eee;
  }
  .com-input {
    width: 210px;
  }
 </style>
 